export default {
	data() {
		return {
			
		}
	},
	methods: {
		handleGetUserInfo(res){
			console.log(res)
			if(res.mp.detail.userInfo){
				console.log(res.mp.detail.userInfo)
				wx.showToast({
					title:'登录成功',
					// duration: 5000,  // 貌似不生效
					success: () => {
						// reLaunch解决跳转能tabBar传参的问题
						wx.reLaunch({
							url: '/pages/personal/personal?userInfo=' + JSON.stringify(res.mp.detail.userInfo)
						})
					}
				})
				
			}else {
				wx.showToast({
					title: '授权失败，请重新授权'
				})
			}
		}
	}
}
